import {Component} from 'react'

class CounterButton extends Component {
    constructor(props) {
        super(props);
        this.state = {
            count: 0
        }
    }

    handleClick = () => {
        this.setState( state => {
            count: state.count + 1;
        });
    }
    
    render() {
        return (
            <button
                onClick = {this.handleClick}
            >
                Count: {this.state.count}
            </button>
        )
    }
}

export default CounterButton;
 